<template>
    <div>
        <div class="">
            <h3 class="mb-3 text-center"></h3>
                <table class="table">
                            <thead>
                                <th width="30%"></th>
                                <th  class="align-middle"></th>
                                <th></th>
                                <th></th>
                            </thead>
                            <tbody>
                                <tr v-for="item in table.carts">
                                    <td><img width="100px" height="100px" :src="item.product.imageUrl" alt=""></td>
                                    <td class="align-middle">{{item.product.title}}</td>
                                    <td class="align-middle">{{item.qty}}{{item.product.unit}}</td>
                                    <td class="align-middle ">NT.{{item.product.price}}</td>
                                </tr>
                            </tbody>
                            <tfoot>
                                <tr v-if="table.total-table.final_total>0">
                                    <td colspan="3" class="text-right text-danger"></td>
                                    <td class="text-right text-danger">{{table.total-table.final_total|currency}}</td>
                                </tr>
                                <tr>
                                    <td colspan="3" class="text-right"></td>
                                    <td class="text-right">NT{{table.final_total|currency}}</td>
                                </tr>
                            </tfoot>
                    </table>
        </div>
    </div>
</template>

<script>
    export default{
        props:['table']
    }
</script>


